// Input

// Cheakbox

.header-checkbox__list {
  margin-left: auto;
  margin-right: 16px;
  padding: 0;
  display: flex;
  list-style: none;
}

.header-checkbox__icon-light,
.header-checkbox__icon-dark {
  display: none;
}

.header__theme-select {
  display: flex;
  margin-left: auto;
  margin-right: 16px;
  align-items: center;
}

.header__theme-select-light--on {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;

  color: var(--switch-on-color);
}

.header__theme-select-dark--off {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;

  color: var(--switch-off-color);
}

.switch {
  margin-left: 12px;
  margin-right: 12px;
  position: relative;
  display: flex;
  border: 0.6px solid var(--switch-on-color);
  border-radius: 20px;
}

.switch input {
  display: none;
}

.slider {
  width: 40px;
  height: 20px;

  position: absolute;

  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  -webkit-transition: 0.4s;
  transition: 0.4s;
  background-color: var(--light-bg-color);
}

.slider:before {
  position: absolute;
  content: '';
  height: 16px;
  width: 16px;

  left: 1px;
  top: 2px;
  background-color: var(--switch-on-color);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--switch-on-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--switch-on-color);
}

input:checked + .slider:before {
  background-color: var(--light-bg-color);

  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(20px);
}

.slider.round {
  border-radius: 20px;
  flex: none;
  order: 1;
  flex-grow: 0;
}

.slider.round:before {
  border-radius: 50%;

  color: var(--light-bg-color);
}

svg {
  width: 21px;
  height: 21px;
  fill: var(--switch-on-color);
}

// Cheakbox

// Dark theme

.header-links__dark-theme {
  color: var(--dark-theme-text-color);
}

.header-input--dark-theme {
  background-color: #111321;
  border: 1px solid var(--dark-theme-text-color);
}

.header-input__icon--dark-theme {
  fill: var(--dark-theme-text-color);
}

.header__theme-select-light--off {
  color: var(--dark-theme-text-color);
}

.header__theme-select-dark--on {
  color: var(--main-color-blue);
}

